p {
    font-family: 'Poppins', sans-serif;
    padding: 0;
    margin: 0;
}

h1 {
    font-family: 'Poppins', sans-serif;
    padding: 0;
    margin-top: 10px;
}

.container-landing {
    width: 100vw;
    height: 100vh;
    padding: 20px;
}

.image-landing {
    width: auto;
    height: 80vh;
    background-image: url("./img/original.jpg");
    border-radius: 10px;
    background-repeat: no-repeat;
    background-size: cover;
}

.image-landing-text {
    position: absolute;
    bottom: 0vh;
    margin: 0 4% 4% 3%;
    padding: 2px 2px 2px 2px;
    text-align: justify;
    background-color: rgba(244, 245, 248, 0.2);
    border-radius: 2px;
}

.image-landing-text h1 {
    font-size: 14px;
    font-weight: bold;
}

.image-landing-text p {
    font-size: 12px;
    line-height: 2vh;
}

ion-button {
    text-transform: none;
    --border-radius: 7.5px;
    margin: 0;
    font-family: 'Poppins', sans-serif;
    font-size: 13px;
}

ion-label {
    font-family: 'Poppins', sans-serif;
}

@media screen and (min-width: 700px) {
    .image-landing-text p {
        line-height: 3vh;
    }
  }